:root {
  --sky-color: #00acdc;
}

body {
  margin: 0;
  font-family: Roboto, sans-serif;
  font-weight: 500;
}

.nav-list {
  list-style: none;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.logo {
  width: 40px;
  height: 40px;
}

.nav-ul {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav-list:not(:first-child) {
  margin-left: 20px;
}

.nav-link {
  color: #000;
  text-transform: uppercase;
  align-items: center;
  height: 60px;
  font-size: 12px;
  text-decoration: none;
  transition: all .3s;
  display: flex;
  position: relative;
}

.is-active {
  color: var(--sky-color);
}

.is-active:after {
  content: "";
  background-color: var(--sky-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.nav-link:hover, .nav-link:active {
  color: var(--sky-color);
}

